import React from 'react';
import Slider from "react-slick";
import axios from 'axios';

const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

const formatUSD = (n) => {
  return numberWithCommas(parseInt(n))
}

const PoolInfoCard = (props) => {
  const { details } = props;
  return (
    <div className={'pool-card d-flex flex-column'}>
      <div className={'d-flex'}>
        <div className={'mr-2'}>
          <img src={"assets/svg/icon-binance.svg"} />
        </div>
        <div className={'pool-card-info'}>
          <div className={'pool-card-info-mfo'}>{details.name}</div>
          <div className={'pool-card-info-tvl'}>Vault TVL: ${formatUSD(details.tvl)}</div>
          <div className={'pool-card-info-apt'}>APR {details.apy}%</div>
        </div>
      </div>
      <button onClick={(e) => {
        e.preventDefault();
        window.location.href = details.url;
      }} className={'btn btn-primary pool-card-btn'}>Farm now</button>
    </div>
  )
}

const CustomSliderNext = ({ className, style, onClick }) => <img onClick={onClick} className={className} style={{ ...style, opacity: '0.3' }} src={'assets/svg/slider-next-arrow-icon.svg'} />
const CustomSliderPrevious = ({ className, style, onClick }) => <img onClick={onClick} className={className} style={{ ...style, opacity: '0.3' }} src={'assets/svg/slider-previous-arrow-icon.svg'} />

class SectionWelcome extends React.Component {
  settings = {
    dots: false,
    slidesToShow: 2,
    slidesToScroll: 2
  };
  constructor(props) {
    super(props)
    this.state = {
      tvl: 0,
      pool: [],
    }
    axios.get("https://api.dino.exchange/info").then(value => {
      console.log(value)
      this.setState(value.data)
    })
  }
  render() {
    return (
      <section id="welcome">
        <div className="welcome__photo" style={{ backgroundImage: 'url(assets/images/photo-welcome.png)' }}>
          <span className="logo"></span>
          <span className="logo"></span>
          <span className="logo"></span>
          <span className="logo"></span>
          <span className="logo"></span>
          <span className="logo"></span>
          <span className="logo"></span>
        </div>
        <div className="welcome__text">
          <div className="container">
            <div className="row align-items-end">
              <div className="col-lg-6">
                <h1>Become a <br />DeFi Dinosaur <br />with <span>Dino</span></h1>
                <p>Swap, farm, and play games to earn tokens all on one decentralized, community driven platform. <br />Welcome to the Dino World!</p>
                <a href="https://app.dino.exchange" className="btn btn-primary"><span>Enter App</span></a>
                <a href="https://docs.dino.exchange" className="btn btn-secondary" target="_blank" rel="noopener noreferrer"><span>Learn More</span></a>
              </div>
              <div className="col-lg-6">
                <div className={'tvl-title'}>Total Value Locked (TVL)</div>
                <div className={'tvl-value'}>${formatUSD(this.state.tvl)}</div>
                <Slider
                  nextArrow={<CustomSliderNext />}
                  prevArrow={<CustomSliderPrevious />}
                  dots={false}
                  slidesToShow={2}
                  slidesToScroll={2}
                >
                  {this.state.pool.map(item => (
                    <PoolInfoCard details={item} />
                  ))}
                </Slider>
              </div>
            </div>
          </div>
        </div>

      </section>
    );
  }
}

export default SectionWelcome
